.Header {
    position: fixed;
    width: 100%;
    background: #eceeef;
    height: 48px;
    left: 0px;
    top: 0px;
    margin: 0px;
    border: 2px solid #bfc5c7;
    border-style: none none solid none;
    box-sizing: border-box;
    z-index: 9;
}

.Header-title {
    position: relative;
    float: left;
    font-size: 1.3em;
    padding: .6em;
    padding-right: .4em;
    color: #205c99;
    font-weight: bold;
}

.Header-subTitle {
    position: relative;
    float: left;
    font-size: 1em;
    padding: 1em;
    color: #205c99;
    padding-left: 0;
}

.Header-tip {
    position: relative;
    width: auto;
    float: right;
    padding: inherit;
    padding: 16px 4px 0px 0px;
    font-size: 12px;
}

.Header .react-datepicker-wrapper {
    position: relative;
    float: right;
    margin: 6px;
    text-align: center;
    width: auto;
}

.Header .react-datepicker-wrapper input {
    padding: 6px;
    font-size: 14px;
    text-align: center;
    background: #205c99;
    color: white;
    border: none;
    border-radius: 64px;
    outline: none;
    font-weight: bold;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.Header .react-datepicker-popper {
    left: -32px !important;
}

.Header-datepicker {
    cursor: pointer;
}

.Header-tip__clickColors {
    position: relative;
    float: left;
    width: 100%;
    min-height: 32px;
    background: #f9f5bf;
    top: 0px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, .7);
}

.Header-tip__clickColors p {
    position: relative;
    float: left;
    font-size: 14px;
    padding: 12px;
}

.Header-tip__clickColors button {
    position: relative;
    float: right;
    padding: 4px;
    margin: 5px;
    width: 64px;
    font-weight: 700;
    cursor: pointer;
}

.Header-tip__clickColors p b {
    font-weight: bold;
}